
class MarvelService {
    _apiBase = 'https://gateway.marvel.com:443/v1/public/';
    _apiKey = 'apikey=fbd47ae6b9994a4a0c315265d71e65f9';
    _baseOffset = 210;

    getResources = async (url) => {
        let res = await fetch(url);

        if(!res.ok){
            throw new Error (`could not fetch ${url}, status ${res.status}`)
        }

        return await res.json();
    }

    getAllCharacters = async (offset = this._baseOffset) => {
        const res = await this.getResources(`${this._apiBase}characters?limit=9&offset=${offset}&${this._apiKey}`);
        return res.data.results.map(this._transformCharacter)
    }

    getCharacter = async (id) => {
        const res = await this.getResources(`${this._apiBase}characters/${id}?${this._apiKey}`);
        return this._transformCharacter(res.data.results[0])
    }

    _transformCharacter = (char) => {
        console.log(char)
        return {
            id: char.id,
            name: char.name,
            description: (char.description.length > 200 ? char.description.slice(0, 200) + '...' : char.description) 
            || 'No description found',
            thumbnail: `${char.thumbnail.path}.${char.thumbnail.extension}`,
            homepage: char.urls[0].url,
            wiki: char.urls[1].url,
            comics: char.comics.items,
        }
    }
}

export default MarvelService;